import style from "./Form.module.css";
import { useRef, useState } from "react";


const Form =(props)=>{

    const inputRef=useRef();
    const [didInput, setDidInput]=useState(false);
    const [priority1, setPriority1]=useState(false);
    const [priority2, setPriority2]=useState(false);
    const [priority3, setPriority3]=useState(false);
    const[priorityContent,setPriorityContent]=useState("");
    const[textcolor,setTextColor]=useState("default");


    const userInputHandler=(e)=>{
        const text=e.target.value.trim();
        if(text.length!==0){
            setDidInput(true);
        }else{
            setDidInput(false);
        }

    }

    
    const p1Handler=()=>{
        setPriority1(true);
        setPriority3(false);
        setPriority2(false);
        setPriorityContent(1);
        setTextColor("#ff7707");

    };

    const p2Handler=()=>{
        setPriority2(true);
        setPriority3(false);
        setPriority1(false)
        setPriorityContent(2);
        setTextColor("#f8b436");

    };

    const p3Handler=()=>{
        setPriority3(true);
        setPriority2(false);
        setPriority1(false)
        setPriorityContent(3);
        setTextColor("#e9e2a4");
    };





    const submitHandler=(event)=>{
        event.preventDefault();
        const inputvalue=inputRef.current.value;
        
        if(inputvalue.trim().length!==0){
            let priority=0;
            if(priority1){priority=1;}
            if(priority2){priority=2;}
            if(priority3){priority=3;}
            props.addlist(inputvalue, priority);
            inputRef.current.value="";
            
        }
        setDidInput(false);
        setPriority3(false);
        setPriority2(false);
        setPriority1(false);
        setTextColor("black");
    };


    return (
        <div className={style.form}>
            {didInput &&(
                <div className={style.colorboxcontainer}>            
                <div className={`${style.colorchoosebox} ${style.alist}`} onClick={p1Handler}>{priority1 &&priorityContent}</div>
                <div className={`${style.colorchoosebox} ${style.blist}`} onClick={p2Handler}>{priority2 &&priorityContent}</div>
                <div className={`${style.colorchoosebox} ${style.clist}`} onClick={p3Handler}>{priority3 &&priorityContent}</div>
            </div>)}
            <form onSubmit={submitHandler}>
                <label className={style.label} htmlFor="goal">What you want to do</label>
                <input  
                    ref={ inputRef } 
                    className={style.input} 
                    onChange={userInputHandler} 
                    id="goal" 
                    type="text"
                    style={{color:textcolor}}
                    />
               {didInput && <button type="submit" className={style.button}>Add</button>} 
            </form>
        </div>
    );
}

export default Form;